@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
/* --- CSS RESET --- */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  box-sizing: border-box;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
  box-sizing: border-box;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* --- CSS RESET END --- */

/* --- CSS VARIABLES --- */
/* https://developer.mozilla.org/en-US/docs/Web/CSS/var */
:root {
  --text-color: black;
  /* #2a8c47; */
  --green-color: grey;
}

/* GENERAL STYLES */

body {
  font-family: "Roboto", "Verdana", sans-serif;
  font-weight: 400;
  line-height: normal;
}
a {
  text-decoration: none;
  color: black;
  color: var(--text-color);
}

a:hover {
  color: grey;
  color: var(--green-color);
}

h1 {
  font-size: 70px;
  margin-bottom: 20px;
}

h2 {
  color: black;
  color: var(--text-color);
  font-size: 8px;
  display: inline;
  text-transform: uppercase;
  letter-spacing: 6px;
}

h3 {
  color: black;
  color: var(--text-color);
  font-size: 26px;
}
h4 {
  color: black;
  color: var(--text-color);
  font-size: 20px;
}
th,
td {
  padding: 5px;
  text-align: left;
}

.sessionicon {
  width: 20px;
  height: auto;
}

/* END GENERAL STYLES */

